import React, { useEffect, useState, useRef, useCallback } from 'react';
import UsersList from './UsersList/UsersList';
import Messenger from './Messenger/Messenger';
import styles from './Chat.module.css';
import { getUnreadMessages, getUsers } from "../api/api";
import { enqueueSnackbar, closeSnackbar } from 'notistack';
import Notification from "../components/Notification/Notification";
import socket from "../api/socket";

const Chat = () => {
    const [selectedUser, setSelectedUser] = useState(null);
    const [users, setUsers] = useState([]);
    const [isUserListVisible, setIsUserListVisible] = useState(true);
    const [unreadCounts, setUnreadCounts] = useState({});
    const [newMessages, setNewMessages] = useState({});
    const addedMessagesRef = useRef({});
    const [isSocketConnected, setIsSocketConnected] = useState(false);

    const toggleUserList = () => {
        if (!isUserListVisible) {
            setUnreadCounts((prevCounts) => ({
                ...prevCounts,
                [selectedUser.id]: 0,
            }));

            setSelectedUser(null);
        }
        setIsUserListVisible(!isUserListVisible);
    };

    const handleUserSelect = (user) => {
        setSelectedUser(user);
        setIsUserListVisible(false);

        setUnreadCounts((prevCounts) => ({
            ...prevCounts,
            [user.id]: 0,
        }));
    };

    const handleNewMessage = useCallback((update) => {
        console.log('New message received:', update);
        if (update.message.sender !== 0) {
            setUnreadCounts((prevCounts) => ({
                ...prevCounts,
                [update.user]: (prevCounts[update.user] || 0) + 1,
            }));
            const user = users.find(u => u.id === update.user);
            if (user !== selectedUser) {
                enqueueSnackbar(
                    <Notification
                        user={user}
                        message={update.message.text}
                        onClickOpen={() => {
                            const user = users.find(u => u.id === update.user);
                            if (user) {
                                handleUserSelect(user);
                                closeSnackbar();
                            }
                        }}
                        onClickOk={() => closeSnackbar()}
                    />,
                );
            }
        } else {
            setUnreadCounts((prevCounts) => ({
                ...prevCounts,
                [update.user]: 0,
            }));
        }

        setNewMessages((prevMessages) => {
            const senderMessages = prevMessages[update.user] || [];
            if (!senderMessages.some(msg => msg.id === update.message.id)) {
                const updatedMessages = {
                    ...prevMessages,
                    [update.user]: [...senderMessages, update.message],
                };
                let unreadMessages = JSON.parse(sessionStorage.getItem(`unreadMessages_${update.user}`) || '[]');
                if (!Array.isArray(unreadMessages)) {
                    unreadMessages = [];
                }
                unreadMessages.push(update.message);
                sessionStorage.setItem(`unreadMessages_${update.user}`, JSON.stringify(unreadMessages));

                return updatedMessages;
            }
            console.log('Contain:', newMessages);
            return prevMessages;
        });
    }, [newMessages, users, selectedUser]);

    useEffect(() => {
        // ÐžÐ±Ñ€Ð°Ð±Ð¾Ñ‚ÐºÐ° Ð½Ð¾Ð²Ð¾Ð³Ð¾ ÑÐ¾Ð¾Ð±Ñ‰ÐµÐ½Ð¸Ñ
        socket.on('newMessage', handleNewMessage);
        return () => {
            socket.off('newMessage', handleNewMessage);
        };
    }, [handleNewMessage]);

    useEffect(() => {
        // ÐžÑ‚ÑÐ»ÐµÐ¶Ð¸Ð²Ð°Ð½Ð¸Ðµ ÑÐ¾ÑÑ‚Ð¾ÑÐ½Ð¸Ñ Ð¿Ð¾Ð´ÐºÐ»ÑŽÑ‡ÐµÐ½Ð¸Ñ Ðº ÑÐ¾ÐºÐµÑ‚Ñƒ
        socket.on('connect', () => {
            setIsSocketConnected(true);
        });

        socket.on('disconnect', () => {
            setIsSocketConnected(false);
        });

        return () => {
            socket.off('connect');
            socket.off('disconnect');
        };
    }, []);

    useEffect(() => {
        // Ð—Ð°Ð³Ñ€ÑƒÐ·ÐºÐ° Ð½ÐµÐ¿Ñ€Ð¾Ñ‡Ð¸Ñ‚Ð°Ð½Ð½Ñ‹Ñ… ÑÐ¾Ð¾Ð±Ñ‰ÐµÐ½Ð¸Ð¹ Ð¿Ñ€Ð¸ Ð¼Ð¾Ð½Ñ‚Ð¸Ñ€Ð¾Ð²Ð°Ð½Ð¸Ð¸ ÐºÐ¾Ð¼Ð¿Ð¾Ð½ÐµÐ½Ñ‚Ð°
        const fetchUnreadMessages = async () => {
            try {
                const unreadMessages = await getUnreadMessages();

                // Ð“Ñ€ÑƒÐ¿Ð¿Ð¸Ñ€ÑƒÐµÐ¼ ÑÐ¾Ð¾Ð±Ñ‰ÐµÐ½Ð¸Ñ Ð¿Ð¾ Ð¿Ð¾Ð»ÑŒÐ·Ð¾Ð²Ð°Ñ‚ÐµÐ»ÑŽ Ð¸ Ð¾Ð±Ð½Ð¾Ð²Ð»ÑÐµÐ¼ ÑÑ‡ÐµÑ‚Ñ‡Ð¸Ðº
                const groupedMessages = unreadMessages.reduce((acc, message) => {
                    const userId = message.user;
                    if (!acc[userId]) {
                        acc[userId] = { messages: [], count: 0 };
                    }
                    acc[userId].messages.push(message.message);
                    acc[userId].count += 1;
                    return acc;
                }, {});

                // Ð¡Ð¾Ñ…Ñ€Ð°Ð½ÑÐµÐ¼ ÑÐ¾Ð¾Ð±Ñ‰ÐµÐ½Ð¸Ñ Ð² sessionStorage
                Object.keys(groupedMessages).forEach(userId => {
                    const { messages, count } = groupedMessages[userId];
                    sessionStorage.setItem(`unreadMessages_${userId}`, JSON.stringify(messages));

                    // ÐžÐ±Ð½Ð¾Ð²Ð»ÑÐµÐ¼ ÑÐ¾ÑÑ‚Ð¾ÑÐ½Ð¸Ðµ unreadCounts
                    setUnreadCounts((prevCounts) => ({
                        ...prevCounts,
                        [userId]: count,
                    }));
                });

            } catch (error) {
                console.error('Error fetching unread messages:', error);
            }
        };

        fetchUnreadMessages();
    }, []);

    useEffect(() => {
        const fetchUsers = async () => {
            const usersData = await getUsers();
            setUsers(usersData);
        };
        fetchUsers();
        if (window.Telegram && window.Telegram.WebApp) {
            window.Telegram.WebApp.ready()
            window.Telegram.WebApp.enableClosingConfirmation();
            window.Telegram.WebApp.disableVerticalSwipes();
            window.Telegram.WebApp.expand();
        }
    }, []);

    if (!isSocketConnected) {
        return (
            <div className={styles.connectingContainer}>
                <div className={styles.connecting}>
                    <div className={styles.serverIcon}></div>
                    <div className={styles.connectingArrows}>
                        <div className={styles.connectingArrow}></div>
                        <div className={styles.connectingArrow}></div>
                        <div className={styles.connectingArrow}></div>
                    </div>
                </div>
                <div className={styles.connecting}>
                    ÐŸÐ¾Ð´ÐºÐ»ÑŽÑ‡ÐµÐ½Ð¸Ðµ
                    <span className={styles.connectingDot}>.</span>
                    <span className={styles.connectingDot}>.</span>
                    <span className={styles.connectingDot}>.</span>

                </div>
            </div>
        );
    }

                return (
                <div className={styles.chatContainer}>
                    <div className={`${styles.userList} ${isUserListVisible ? styles.visible : ''}`}>
                        <UsersList users={users} onSelectUser={handleUserSelect} unreadCounts={unreadCounts}/>
                    </div>
                    <div className={styles.chat}>
                        {selectedUser ? (
                            <Messenger user={selectedUser} toggleUserList={toggleUserList} newMessages={newMessages}
                                       addedMessagesRef={addedMessagesRef} socket={socket}/>
                        ) : (
                            <p>Ð’Ñ‹Ð±ÐµÑ€Ð¸Ñ‚Ðµ Ð¿Ð¾Ð»ÑŒÐ·Ð¾Ð²Ð°Ñ‚ÐµÐ»Ñ, Ñ‡Ñ‚Ð¾Ð±Ñ‹ Ð½Ð°Ñ‡Ð°Ñ‚ÑŒ Ð¾Ð±Ñ‰ÐµÐ½Ð¸Ðµ.</p>
                        )}
                    </div>
                </div>
                );
                };

                export default Chat;