import React from "react";


import styles from "./Notification.module.css";

const Notification = ({ user, message, onClickOpen, onClickOk }) => (
    <div className={styles.notification}>
        <div onClick={onClickOpen} className={styles.notificationContent}>
            <div className={styles.notificationUser}>{user.name} {user.surname}</div>
            <div className={styles.notificationText}>{message}</div>
        </div>
        <div onClick={onClickOk} className={styles.notificationClose}>
        ÐžÐº
        </div>
    </div>
);

export default Notification;