// App.js

import styles from './App.module.css';
import React from 'react';
import Chat from './blocks/Chat';
import {SnackbarProvider} from 'notistack';


function App() {
    return (
        <div className={styles.app}>
          <SnackbarProvider className={styles.transparentSnackbar} maxSnack={1} anchorOrigin={{ vertical: 'top', horizontal: 'center' }} preventDuplicate autoHideDuration={3000}>
            <Chat/>
          </SnackbarProvider>
        </div>
    );
}

export default App;
