import axios from 'axios';

const FAST_API_URL = window.env.FAST_API_URL;
const API_KEY = window.env.API_KEY;
const API_URL = `${FAST_API_URL}/api`;
const TELEGRAM_API_URL = `https://api.telegram.org/bot${API_KEY}`;
const TELEGRAM_FILE_URL = `https://api.telegram.org/file/bot${API_KEY}`;

const axiosInstance = axios.create({
    baseURL: API_URL,
    headers: {
        'Authorization': `Bearer ${API_KEY}`
    }
});

export const getUsers = async () => {
    const response = await axiosInstance.get('/users')
    return response.data.users;
};

export const getUnreadMessages = async () => {
    const response = await axiosInstance.get('/unreadMessages')
    return response.data.unread_messages;
};

export const getMessages = async (user_id) => {
    const response = await axiosInstance.get(`/messages/${user_id}`);
    return response.data.messages;
};

export const markMessagesAsRead = async (userId) => {
    await axiosInstance.post(`/markMessagesAsRead/${userId}`);
};


export const getFileUrl = async (fileId) => {
    const url = `${TELEGRAM_API_URL}/getFile`;
    const response = await axios.post(url, {
        file_id: fileId,
    });

    return `${TELEGRAM_FILE_URL}/${response.data.result.file_path}`;
}


export const sendMessage = async (userId, message) => {
    const response = await axios.post(`${TELEGRAM_API_URL}/sendMessage`, {
        chat_id: userId,
        text: message,
    });
    return response.data.result;
};


const sendPhoto = async (chatId, photo, caption = '') => {
    const url = `${TELEGRAM_API_URL}/sendPhoto`;
    const formData = new FormData();
    formData.append('chat_id', chatId);
    formData.append('photo', photo);
    if (caption) {
        formData.append('caption', caption);
    }

    const response = await axios.post(url, formData, {
        headers: {
            'Content-Type': 'multipart/form-data',
        },
    });

    const fileInfo = response.data.result.photo[response.data.result.photo.length - 1];
    const fileId = fileInfo.file_id;
    const fileSize = fileInfo.file_size;

    response.data.result.document =
        {
            file_name: photo.name,
            file_id: fileId,
            file_size: fileSize,
            mime_type: photo.type,
        }

    return response.data.result;
};


