const sendDocument = async (chatId, document, caption = '') => {
    const url = `${TELEGRAM_API_URL}/sendDocument`;
    const formData = new FormData();
    formData.append('chat_id', chatId);
    formData.append('document', document);
    if (caption) {
        formData.append('caption', caption);
    }

    const response = await axios.post(url, formData, {
        headers: {
            'Content-Type': 'multipart/form-data',
        },
    });

    return response.data.result;
};


export const sendMessageWithFile = async (chatId, file, caption = '') => {
    const mimeType = file.type;

    if (mimeType.startsWith('image/')) {
        return sendPhoto(chatId, file, caption);
    } else if (mimeType.startsWith('video/')) {
        return sendVideo(chatId, file, caption);
    } else {
        return sendDocument(chatId, file, caption);
    }
};


export const sendMediaGroup = async (chatId, files, caption = '') => {
    const formData = new FormData();
    formData.append('chat_id', chatId);

    const media = files.map((file, index) => {
        const type = file.type.startsWith('image/') ? 'photo' : file.type.startsWith('video/') ? 'video' : 'document';
        const randomName = `random-name-${index}`;
        formData.append(randomName, file);
        return {
            type: type,
            media: `attach://${randomName}`,
            caption: index === 0 ? caption : '', // Ð”Ð¾Ð±Ð°Ð²Ð»ÑÐµÐ¼ Ð¿Ð¾Ð´Ð¿Ð¸ÑÑŒ Ñ‚Ð¾Ð»ÑŒÐºÐ¾ Ðº Ð¿ÐµÑ€Ð²Ð¾Ð¼Ñƒ Ñ„Ð°Ð¹Ð»Ñƒ
            parse_mode: 'HTML', // Ð•ÑÐ»Ð¸ Ð½ÑƒÐ¶Ð½Ð¾ Ð¸ÑÐ¿Ð¾Ð»ÑŒÐ·Ð¾Ð²Ð°Ñ‚ÑŒ HTML-Ñ€Ð°Ð·Ð¼ÐµÑ‚ÐºÑƒ Ð² Ð¿Ð¾Ð´Ð¿Ð¸ÑÐ¸
        };
    });

    formData.append('media', JSON.stringify(media));

    const response = await axios.post(`${TELEGRAM_API_URL}/sendMediaGroup`, formData, {
        headers: {
            'Content-Type': 'multipart/form-data',
        },
    });

    // Ð”Ð¾Ð±Ð°Ð²Ð»ÑÐµÐ¼ Ð¸Ð½Ñ„Ð¾Ñ€Ð¼Ð°Ñ†Ð¸ÑŽ Ð¾ Ñ„Ð°Ð¹Ð»Ð°Ñ… Ð² Ð¾Ñ‚Ð²ÐµÑ‚
    response.data.result.forEach((item, index) => {
        const fileInfo = item.photo ? item.photo[item.photo.length - 1] : item.video ? item.video : item.document;
        const fileId = fileInfo.file_id;
        const fileSize = fileInfo.file_size;

        item.document = {
            file_name: files[index].name,
            file_id: fileId,
            file_size: fileSize,
            mime_type: files[index].type,
        };
    });

    console.log(response.data.result);
    return response.data.result;
};