// Messenger.jsx

import React, { useState, useEffect, useRef } from 'react';
import MessageInput from '../../components/MessageInput/MessageInput';
import Message from "../../components/Message/Message";
import styles from './Messenger.module.css';
import formatDate from "./utils/formatDate";
import { getMessages, markMessagesAsRead } from '../../api/api';
import ScrollDown from "../../components/ui/scrollDown/ScrollDown";

const Messenger = ({ user, toggleUserList, newMessages, addedMessagesRef, socket }) => {
    const [messages, setMessages] = useState([]);
    const [showScrollButton, setShowScrollButton] = useState(false);
    const messagesEndRef = useRef(null);
    const messagesContainerRef = useRef(null);

    useEffect(() => {
        if (user) {
            const fetchMessages = async () => {
                const storedMessages = JSON.parse(sessionStorage.getItem(`messages_${user.id}`) || '[]');
                const unreadMessages = JSON.parse(sessionStorage.getItem(`unreadMessages_${user.id}`) || '[]');

                let combinedMessages = [...storedMessages, ...unreadMessages];

                if (!(user.id in addedMessagesRef.current)) {
                    const oldMessages = await getMessages(user.id);
                    combinedMessages = [...oldMessages, ...combinedMessages];
                    addedMessagesRef.current[user.id] = true;
                    console.log("old", oldMessages);
                }

                const uniqueMessages = Array.from(new Set(combinedMessages.map(msg => msg.id)))
                    .map(id => combinedMessages.find(msg => msg.id === id));

                setMessages(uniqueMessages);
                console.log("stored", storedMessages);
                console.log("new messages", unreadMessages);
                console.log("combined", uniqueMessages);

                // ÐŸÐ¾Ð¼ÐµÑ‡Ð°ÐµÐ¼ Ð²ÑÐµ ÑÐ¾Ð¾Ð±Ñ‰ÐµÐ½Ð¸Ñ ÐºÐ°Ðº Ð¿Ñ€Ð¾Ñ‡Ð¸Ñ‚Ð°Ð½Ð½Ñ‹Ðµ
                await markMessagesAsRead(user.id);

                // ÐžÑ‡Ð¸Ñ‰Ð°ÐµÐ¼ Ð½ÐµÐ¿Ñ€Ð¾Ñ‡Ð¸Ñ‚Ð°Ð½Ð½Ñ‹Ðµ ÑÐ¾Ð¾Ð±Ñ‰ÐµÐ½Ð¸Ñ Ð¸Ð· sessionStorage
                sessionStorage.setItem(`messages_${user.id}`, JSON.stringify(uniqueMessages));
                sessionStorage.removeItem(`unreadMessages_${user.id}`);
                console.log('combined was saved on storage', sessionStorage.getItem(`messages_${user.id}`));
                console.log('new messages was deleted', sessionStorage.getItem(`unreadMessages_${user.id}`));
            };

            fetchMessages();
        }
    }, [user, addedMessagesRef]);

    useEffect(() => {
        setTimeout(() => {
            scrollToBottom({ behavior: "instant" }); // ÐŸÑ€Ð¾ÐºÑ€ÑƒÑ‚ÐºÐ° Ð¿Ñ€Ð¸ Ð¸Ð·Ð¼ÐµÐ½ÐµÐ½Ð¸Ð¸ ÑÐ¾ÑÑ‚Ð¾ÑÐ½Ð¸Ñ messages
        }, 0);
    }, [messages]);

    useEffect(() => {
        if (user && newMessages && newMessages[user.id]) {
            console.log("for user ", user.id, " message was handled")
            setMessages((prevMessages) => {
                const updatedMessages = [...prevMessages];
                newMessages[user.id].forEach(newMessage => {
                    if (!prevMessages.some(msg => msg.id === newMessage.id)) {
                        updatedMessages.push(newMessage);
                    }
                });
                console.log("for user ", user.id, " messages was updated: ", updatedMessages)
                return updatedMessages;
            });
        }
    }, [newMessages, user, addedMessagesRef]);

    const scrollToBottom = (behavior) => {
        void messagesEndRef.current?.scrollIntoView(behavior);
    };

    const handleSendMessage = (newMessage) => {
        setMessages((prevMessages) => {
            const updatedMessages = newMessage.old_id === null
                ? [...prevMessages, newMessage]
                : prevMessages.map((msg) =>
                    msg.id === newMessage.old_id ? newMessage : msg
                );
            sessionStorage.setItem(`messages_${user.id}`, JSON.stringify(updatedMessages));

            return updatedMessages;
        });
    };

    useEffect(() => {
        const observer = new IntersectionObserver(
            ([entry]) => {
                setShowScrollButton(!entry.isIntersecting);
            },
            {
                root: messagesContainerRef.current,
                rootMargin: '0px 0px 2000px 0px', // Ð£Ð²ÐµÐ»Ð¸Ñ‡Ð¸Ð²Ð°ÐµÐ¼ Ð¾Ð±Ð·Ð¾Ñ€ Ð²Ð¸Ð´Ð¸Ð¼Ð¾ÑÑ‚Ð¸ ÑÐ²ÐµÑ€Ñ…Ñƒ Ð½Ð° 50px Ð¸ ÑÐ½Ð¸Ð·Ñƒ Ð½Ð° 100px
                threshold: 0.1,
            }
        );

        const currentMessagesEndRef = messagesEndRef.current; // Ð¡Ð¾Ñ…Ñ€Ð°Ð½ÑÐµÐ¼ Ñ‚ÐµÐºÑƒÑ‰ÐµÐµ Ð·Ð½Ð°Ñ‡ÐµÐ½Ð¸Ðµ

        if (currentMessagesEndRef) {
            observer.observe(currentMessagesEndRef);
        }

        return () => {
            if (currentMessagesEndRef) {
                observer.unobserve(currentMessagesEndRef);
            }
        };
    }, [messages]);

    return (
        <div className={styles.mainWindow}>
            <div className={styles.container}>
                <div className={styles.header}>

                    <div className={styles.connectionStatus} onClick={toggleUserList}>
                        <div className={styles.burgerMenu}>
                            <div className={styles.burgerLine}></div>
                            <div className={styles.burgerLine}></div>
                            <div className={styles.burgerLine}></div>
                        </div>
                    </div>
                    <div className={styles.infoContainer}>
                        <div className={styles.userFullName}>{user.name} {user.surname}</div>
                        <div className={styles.chatId}>{user.id}</div>
                    </div>
                    <div className={styles.burgerMenu}>
                    </div>

                </div>
                <div className={styles.content}>
                    <div className={styles.messages} ref={messagesContainerRef}>
                        {messages
                            .slice() // Ð¡Ð¾Ð·Ð´Ð°ÐµÐ¼ ÐºÐ¾Ð¿Ð¸ÑŽ Ð¼Ð°ÑÑÐ¸Ð²Ð°, Ñ‡Ñ‚Ð¾Ð±Ñ‹ Ð½Ðµ Ð¸Ð·Ð¼ÐµÐ½ÑÑ‚ÑŒ Ð¾Ñ€Ð¸Ð³Ð¸Ð½Ð°Ð»ÑŒÐ½Ñ‹Ð¹ Ð¼Ð°ÑÑÐ¸Ð²
                            .sort((a, b) => a.date - b.date) // Ð¡Ð¾Ñ€Ñ‚Ð¸Ñ€ÑƒÐµÐ¼ ÑÐ¾Ð¾Ð±Ñ‰ÐµÐ½Ð¸Ñ Ð¿Ð¾ Ð´Ð°Ñ‚Ðµ
                            .map((message, index) => {
                                const currentDate = new Date(message.date * 1000);
                                const prevMessage = messages[index - 1];
                                const prevDate = prevMessage ? new Date(prevMessage.date * 1000) : null;

                                const showDate = !prevDate || currentDate.toDateString() !== prevDate.toDateString();

                                return (
                                    <React.Fragment key={message.id}>
                                        {showDate && (
                                            <div className={styles.dateSeparator}>
                                                <span className={styles.dateSeparatorText}>
                                                    {formatDate(message.date)}
                                                </span>
                                            </div>
                                        )}
                                        <Message message={message}/>
                                    </React.Fragment>

                                );
                            })
                        }
                        <div ref={messagesEndRef}> </div>
                    </div>
                    <ScrollDown showScrollButton={showScrollButton} onClick={() => scrollToBottom({behavior: "smooth"})}/>
                </div>
                <MessageInput userId={user.id} onSendMessage={handleSendMessage} socket={socket} />
            </div>
        </div>
    );
};

export default Messenger;