import React, { useState, useRef, useEffect } from 'react';
import { sendMessage, sendMessageWithFile, sendMediaGroup} from '../../api/api';


import styles from './MessageInput.module.css';
import ClipIcon from "../ui/icons/ClipIcon";
import SendIcon from "../ui/icons/SendIcon";

const MessageInput = ({ userId, onSendMessage, socket}) => {
    const [message, setMessage] = useState('');
    const [attachedFiles, setAttachedFiles] = useState([]);

    const textareaRef = useRef(null);
    const fileInputRef = useRef(null);
    const messageIdCounter = useRef(1);

    const generateUniqueId = () => {
        return messageIdCounter.current++;
    };

    useEffect(() => {
        const { message, files } = loadStateFromSessionStorage(userId);
        setMessage(message);
        setAttachedFiles(files);
        console.log(files);
    }, [userId]);

    useEffect(() => {
        adjustTextareaHeight();
    }, [message]);

    const handleSendMessage = async () => {
        if (message.trim() || attachedFiles.length > 0) {
            // Ð“ÐµÐ½ÐµÑ€Ð¸Ñ€ÑƒÐµÐ¼ ÑƒÐ½Ð¸ÐºÐ°Ð»ÑŒÐ½Ñ‹Ð¹ id Ð´Ð»Ñ ÑÐ¾Ð¾Ð±Ñ‰ÐµÐ½Ð¸Ñ
            const uniqueId = generateUniqueId();

            // Ð¡Ð½Ð°Ñ‡Ð°Ð»Ð° Ð´Ð¾Ð±Ð°Ð²Ð»ÑÐµÐ¼ ÑÐ¾Ð¾Ð±Ñ‰ÐµÐ½Ð¸Ðµ Ð² Ð»Ð¾ÐºÐ°Ð»ÑŒÐ½Ð¾Ðµ ÑÐ¾ÑÑ‚Ð¾ÑÐ½Ð¸Ðµ Ñ ÑƒÐ½Ð¸ÐºÐ°Ð»ÑŒÐ½Ñ‹Ð¼ id
            const newMessage = {
                id: uniqueId,
                text: message,
                status: 'sending',
                sender: 0,
                recipient: userId,
                date: Date.now() / 1000, // Ð”Ð¾Ð±Ð°Ð²Ð»ÑÐµÐ¼ Ñ‚ÐµÐºÑƒÑ‰ÑƒÑŽ Ð´Ð°Ñ‚Ñƒ Ð¸ Ð²Ñ€ÐµÐ¼Ñ Ð² Ñ„Ð¾Ñ€Ð¼Ð°Ñ‚Ðµ Unix timestamp
                files: [],
                old_id: null,
            };
            if (attachedFiles.length > 0) {
                const newMessageOnFiles = {...newMessage, files_count: attachedFiles.length};
                onSendMessage(newMessageOnFiles);
            } else {
                onSendMessage(newMessage);
            }
            delete newMessage.files_count;

            setMessage('');
            setAttachedFiles([]);
            saveStateToSessionStorage(userId, '', []);

            // Ð—Ð°Ñ‚ÐµÐ¼ Ð¾Ñ‚Ð¿Ñ€Ð°Ð²Ð»ÑÐµÐ¼ ÑÐ¾Ð¾Ð±Ñ‰ÐµÐ½Ð¸Ðµ Ð½Ð° ÑÐµÑ€Ð²ÐµÑ€
            try {
                if (attachedFiles.length > 0) {
                    let fileResponses;
                    if (attachedFiles.length === 1) {
                        // ÐžÑ‚Ð¿Ñ€Ð°Ð²Ð»ÑÐµÐ¼ Ð¾Ð´Ð¸Ð½ Ñ„Ð°Ð¹Ð»
                        const response = await sendMessageWithFile(userId, attachedFiles[0], message);
                        fileResponses = [{
                            file: attachedFiles[0],
                            response: response.document,
                        }];

                        const updateMessage = {
                            ...newMessage,
                            status: 'sent',
                            id: response.message_id,
                            date: response.date,
                            files: fileResponses.map(fileResponse => ({
                                file_name: fileResponse.response.file_name,
                                file_id: fileResponse.response.file_id,
                                file_size: fileResponse.response.file_size,
                                mime_type: fileResponse.response.mime_type,
                            })),
                        }
                        onSendMessage({
                            ...updateMessage,
                            old_id: uniqueId,
                        });

                        delete updateMessage.old_id;

                        socket.emit('updateMessage', {"user": updateMessage.recipient, "message": updateMessage});
                    } else {
                        // ÐžÑ‚Ð¿Ñ€Ð°Ð²Ð»ÑÐµÐ¼ Ð½ÐµÑÐºÐ¾Ð»ÑŒÐºÐ¾ Ñ„Ð°Ð¹Ð»Ð¾Ð²

                        const response = await sendMediaGroup(userId, attachedFiles, message);

                        fileResponses = response.map((item, index) => ({
                            file: attachedFiles[index],
                            response: item.document || item.photo || item.video,
                        }));

                        const updateMessage = {
                            ...newMessage,
                            status: 'sent',
                            id: response[0].message_id, // Ð˜ÑÐ¿Ð¾Ð»ÑŒÐ·ÑƒÐµÐ¼ message_id Ð¿ÐµÑ€Ð²Ð¾Ð³Ð¾ ÑÐ¾Ð¾Ð±Ñ‰ÐµÐ½Ð¸Ñ Ð² Ð³Ñ€ÑƒÐ¿Ð¿Ðµ
                            date: response[0].date, // Ð˜ÑÐ¿Ð¾Ð»ÑŒÐ·ÑƒÐµÐ¼ Ð´Ð°Ñ‚Ñƒ Ð¿ÐµÑ€Ð²Ð¾Ð³Ð¾ ÑÐ¾Ð¾Ð±Ñ‰ÐµÐ½Ð¸Ñ Ð² Ð³Ñ€ÑƒÐ¿Ð¿Ðµ
                            files: fileResponses.map(fileResponse => ({
                                file_name: fileResponse.response.file_name,
                                file_id: fileResponse.response.file_id,
                                file_size: fileResponse.response.file_size,
                                mime_type: fileResponse.response.mime_type,
                            })),
                        }

                        onSendMessage({
                            ...updateMessage,
                            old_id: uniqueId,
                        });

                        delete updateMessage.old_id;

                        socket.emit('updateMessage', {"user": updateMessage.recipient, "message": updateMessage});
                    }
                } else {
                    // ÐžÑ‚Ð¿Ñ€Ð°Ð²Ð»ÑÐµÐ¼ Ð¿Ñ€Ð¾ÑÑ‚Ð¾ Ñ‚ÐµÐºÑÑ‚Ð¾Ð²Ð¾Ðµ ÑÐ¾Ð¾Ð±Ñ‰ÐµÐ½Ð¸Ðµ
                    const response = await sendMessage(userId, message);
                    // ÐžÐ±Ð½Ð¾Ð²Ð»ÑÐµÐ¼ ÑÑ‚Ð°Ñ‚ÑƒÑ ÑÐ¾Ð¾Ð±Ñ‰ÐµÐ½Ð¸Ñ Ð½Ð° "Ð¾Ñ‚Ð¿Ñ€Ð°Ð²Ð»ÐµÐ½Ð¾" Ð¸ Ð´Ð¾Ð±Ð°Ð²Ð»ÑÐµÐ¼ Ð¸Ð´ÐµÐ½Ñ‚Ð¸Ñ„Ð¸ÐºÐ°Ñ‚Ð¾Ñ€ Ð¾Ñ‚ Telegram

                    const updateMessage = {
                        ...newMessage,
                        status: 'sent',
                        id: response.message_id,
                        date: response.date, // ÐžÐ±Ð½Ð¾Ð²Ð»ÑÐµÐ¼ Ð´Ð°Ñ‚Ñƒ Ð¸ Ð²Ñ€ÐµÐ¼Ñ Ð¿Ñ€Ð¸ ÑƒÑÐ¿ÐµÑˆÐ½Ð¾Ð¹ Ð¾Ñ‚Ð¿Ñ€Ð°Ð²ÐºÐµ
                    }

                    onSendMessage({
                        ...updateMessage,
                        old_id: uniqueId,
                    });

                    delete updateMessage.old_id;

                    socket.emit('updateMessage', {"user": updateMessage.recipient, "message": updateMessage});
                }
            } catch (error) {
                // Ð’ ÑÐ»ÑƒÑ‡Ð°Ðµ Ð¾ÑˆÐ¸Ð±ÐºÐ¸ Ð¾Ð±Ð½Ð¾Ð²Ð»ÑÐµÐ¼ ÑÑ‚Ð°Ñ‚ÑƒÑ Ð½Ð° "Ð¾ÑˆÐ¸Ð±ÐºÐ°"
                onSendMessage({
                    ...newMessage,
                    status: 'error',
                    date: Date.now() / 1000,
                    old_id: uniqueId,
                });
            }
        }
    };

    const adjustTextareaHeight = () => {
        const textarea = textareaRef.current;
        if (textarea) {
            textarea.style.height = '34px';
            textarea.style.height = `${textarea.scrollHeight}px`;
        }
    };

    const handleMessageChange = (e) => {
        const newMessage = e.target.value;
        setMessage(newMessage);
        saveStateToSessionStorage(userId, newMessage, attachedFiles);
    };

    const handleFileChange = (e) => {
        const files = Array.from(e.target.files);
        setAttachedFiles((prevFiles) => [...prevFiles, ...files]);
        saveStateToSessionStorage(userId, message, [...attachedFiles, ...files]);
    };

    const handleFileDelete = (index) => {
        setAttachedFiles((prevFiles) => {
            const newFiles = [...prevFiles];
            newFiles.splice(index, 1);
            return newFiles;
        });

        saveStateToSessionStorage(userId, message, attachedFiles.filter((_, i) => i !== index));
    };

    const saveStateToSessionStorage = (userId, message, files) => {
        sessionStorage.setItem(`message_${userId}`, message);
        const fileObjects = files.map(file => ({
            name: file.name,
            lastModified: file.lastModified,
            size: file.size,
            type: file.type,
        }));
        sessionStorage.setItem(`files_${userId}`, JSON.stringify(fileObjects));
    };

    const loadStateFromSessionStorage = (userId) => {
        const savedMessage = sessionStorage.getItem(`message_${userId}`);
        const savedFiles = sessionStorage.getItem(`files_${userId}`);
        const fileObjects = savedFiles ? JSON.parse(savedFiles) : [];
        const files = fileObjects.map(fileObj => new File([], fileObj.name, {
            lastModified: fileObj.lastModified,
            size: fileObj.size,
            type: fileObj.type,
        }));
        return {
            message: savedMessage || '',
            files: files,
        };
    };


    return (
        <div className={styles.inputContainer}>
            {attachedFiles.length > 0 && (
                <div className={styles.attachedFiles}>
                    {attachedFiles.map((file, index) => (

                        <div key={index} className={styles.attachedFileContainer}>
                            <div className={styles.attachedFileCounterWeight}></div>
                            <div className={styles.attachedFile}>{file.name}</div>
                            <div className={styles.attachedFileCancel} onClick={() => handleFileDelete(index)}>
                                <div className={styles.attachedFileCancelLine}></div>
                                <div className={styles.attachedFileCancelLine}></div>
                            </div>
                        </div>

                    ))}
                </div>
            )}
            <div style={{ display: 'flex', justifyContent: 'space-around', width: '100%', alignItems: 'flex-end' }}>
                <div className={styles.AttachButton}
                     style={{ margin: '0 8px 0 8px', display: 'flex', justifyItems:'center', alignItems: 'center' }}
                     onClick={() => fileInputRef.current.click()}>
                    {(localStorage.getItem('theme') === 'dark') ? (
                        <ClipIcon color={"#8673e0"} width={"32px"} height={"32px"} />
                    ) : (
                        <ClipIcon color={'#338feb'} width={"32px"} height={"32px"} />
                    )}
                </div>
                <textarea
                    ref={textareaRef}
                    className={styles.TextArea}
                    placeholder="ÐÐ°Ð¿Ð¸ÑÐ°Ñ‚ÑŒ ÑÐ¾Ð¾Ð±Ñ‰ÐµÐ½Ð¸Ðµ..."
                    value={message}
                    onChange={handleMessageChange}
                    onInput={adjustTextareaHeight}
                />
                <input
                    type="file"
                    multiple
                    onChange={handleFileChange}
                    className={styles.fileInput}
                    style={{ display: 'none' }}
                    ref={fileInputRef}
                />
                <div className={styles.sendButtonContainer}
                     style={{ margin: '0 8px 0 8px', display: 'flex', justifyItems:'center', alignItems: 'center' }}
                     onClick={handleSendMessage}>
                    {(localStorage.getItem('theme') === 'dark') ? (
                        <SendIcon color={"#8673e0"} width={"32px"} height={"32px"} />
                    ) : (
                        <SendIcon color={"#338feb"} width={"32px"} height={"32px"} />
                    )}
                </div>
            </div>
        </div>
    );
};



export default MessageInput;