import React from 'react';
import styles from './User.module.css';
import UserIcon from "../ui/icons/UserIcon";


const UserComponent = ({ user, onSelectUser, unreadCount }) => {
    return (
        <div className={styles.userCard} onClick={() => onSelectUser(user)}>
            <div className={styles.userInfoContainer}>
                <div className={styles.userIconContainer}>
                    <UserIcon className={styles.userIcon}/>
                </div>
                <div className={styles.userInfo}>
                    <div className={styles.userFullName}>{user.name} {user.surname}</div>
                    <div className={styles.additionInfo}>{user.phone}</div>
                </div>
            </div>
            {unreadCount > 0 && (
                <div className={styles.unreadCount}>
                    <div className={styles.count}>{unreadCount}</div>
                </div>
            )}
        </div>
    );
};


export default UserComponent;