import React, { useState, useEffect } from 'react';
import styles from './Message.module.css';
import { getFileUrl } from '../../api/api';
import Modal from 'react-modal';
import FileIcon from "../ui/icons/FileIcon";

Modal.setAppElement('#root'); // Ð£ÐºÐ°Ð¶Ð¸Ñ‚Ðµ ÐºÐ¾Ñ€Ð½ÐµÐ²Ð¾Ð¹ ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚ Ð´Ð»Ñ Ð¼Ð¾Ð´Ð°Ð»ÑŒÐ½Ð¾Ð³Ð¾ Ð¾ÐºÐ½Ð°

const MessageComponent = ({ message }) => {
    const [loading, setLoading] = useState(true);
    const [files, setFiles] = useState([]);
    const [modalIsOpen, setModalIsOpen] = useState(false);
    const [selectedImage, setSelectedImage] = useState('');

    const messageClass = message.sender === 0 ? styles.rightMessage : styles.leftMessage;

    useEffect(() => {
        const fetchFiles = async () => {
            const urls = await Promise.all(message.files.map(async (file) => {
                if (file.mime_type && file.mime_type.startsWith('image/') && file.file_size > 5 * 1024 * 1024) {
                    return {
                        name: file.file_name,
                        size: file.file_size,
                        mime_type: file.mime_type,
                        file_is_too_big: true,
                    };
                } else if (file.mime_type && file.file_size > 20 * 1024 * 1024) {
                    return {
                        name: file.file_name,
                        size: file.file_size,
                        mime_type: file.mime_type,
                        file_is_too_big: true,
                    };
                }
                const url = await getFileUrl(file.file_id);
                return {
                    name: file.file_name,
                    size: file.file_size,
                    url: url,
                    mime_type: file.mime_type,
                    file_is_too_big: false,
                };
            }));
            setFiles(urls);
            setLoading(false);

        };

        if (message.files.length > 0) {
            fetchFiles();
        }
    }, [message.files]);

    const formatFileSize = (bytes) => {
        if (bytes === 0) return '0 Bytes';
        const k = 1024;
        const sizes = ['Bytes', 'KB', 'MB', 'GB', 'TB'];
        const i = Math.floor(Math.log(bytes) / Math.log(k));
        return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i];
    }

    const formatTime = (unixTime) => {
        const date = new Date(unixTime * 1000);
        const hours = date.getHours().toString().padStart(2, '0');
        const minutes = date.getMinutes().toString().padStart(2, '0');
        return(`${hours}:${minutes}`);
    }

    const openModal = (url) => {
        setSelectedImage(url);
        setModalIsOpen(true);
    };

    const closeModal = () => {
        setModalIsOpen(false);
    };

    return (
        <div className={`${styles.message} ${messageClass}`}>
            {message.files_count > 0 && (
                <div className={styles.fileAttachment}>
                    {Array.from({ length: message.files_count }).map((_, index) => (
                        <div className={styles.shimmerSquare} key={index}></div>
                    ))}
                </div>
            )}
            {message.files.length > 0 && loading ? (
                <div className={styles.fileAttachment}>
                    {Array.from({ length: message.files.length }).map((_, index) => (
                        <div className={styles.shimmerSquare} key={index}></div>
                    ))}
                </div>
            ): files.length > 0 ? (
                <div className={styles.fileAttachment}>
                    {files.map((file, index) => (
                        <div className={styles.file} key={index}>
                            {file.mime_type && file.mime_type.startsWith('image/') && !file.file_is_too_big ? (
                                <div onClick={() => openModal(file.url, 'image')} style={{ cursor: 'pointer' }}>
                                    <img className={styles.image} src={file.url} alt={file.name} />
                                </div>
                            ) : file.mime_type && file.mime_type.startsWith('video/') && !file.file_is_too_big ? (
                                <a href={file.url} download={file.name}>
                                    <video className={styles.video} src={file.url} alt={file.name} controls/>
                                </a>
                            ) : file.mime_type && file.file_is_too_big ? (
                                <div className={styles.document}>
                                    <div>Ð¤Ð°Ð¹Ð» Ð½ÐµÐ´Ð¾ÑÑ‚ÑƒÐ¿ÐµÐ½</div>
                                    <div className={styles.fileName}>{file.name}</div>
                                    <div className={styles.fileSize}>{formatFileSize(file.size)}</div>
                                </div>
                            ) : (
                                <a href={file.url} download={file.name}>
                                    <div className={styles.document}>
                                        <FileIcon className={styles.fileIcon}/>
                                        <div className={styles.fileName}>{file.name}</div>
                                        <div className={styles.fileSize}>{formatFileSize(file.size)}</div>
                                    </div>
                                </a>
                            )}
                        </div>
                    ))}
                </div>
            ) : null}
            <div className={styles.messageText}>{message.text}</div>
            <div className={styles.messageInfo}>
                <span className={styles.dateTime}>{formatTime(message.date)}</span>
                {message.sender === 0 && (
                    <>
                        {message.status === 'sending' && <span className={styles.statusSending}><div className={styles.status}>â—´</div></span>}
                        {message.status === 'sent' && <span className={styles.statusSent}>âœ“</span>}
                        {message.status === 'error' && <span className={styles.statusError}>âœ–</span>}
                    </>
                )}
            </div>

            <Modal
                isOpen={modalIsOpen}
                onRequestClose={closeModal}
                contentLabel="Image Modal"
                style={{
                    content: {
                        maxWidth: '80%',
                        maxHeight: '80%',
                        margin: 'auto',
                    },
                }}
            >
                <img src={selectedImage} alt="Full size" style={{ width: '100%', height: 'auto' }} />
            </Modal>
        </div>
    );
};

export default MessageComponent;